<template>
  <div class='fillCode'>
    <div class='header'>
      <div class='header-left'>
        <img src="../../assets/28doctorLogo.png" alt="">
        <span>胡大一诊所</span>
      </div>
      <div class='header-right'>
        <div class='header-right-textOne pull-left'>首页</div>
        <div class='header-right-textTwo pull-right'>客服电话：400-688-0788</div>
      </div>
    </div>
    <div class='fillCodeForm'>
      <h3>验证手机重置密码</h3>
      <el-form :model="passwordDto" :rules="rules" ref="passwordDto" label-width="100px" class="demo-ruleForm">
        <el-form-item label="手机号码" prop="euMobile">
          <el-input style='width:320px;' v-model='passwordDto.euMobile'></el-input>
        </el-form-item>
        <el-form-item label="验证码" style='display:inline-block;margin-top:11px' prop="code">
          <el-input style='width:180px;' v-model='passwordDto.code' placeholder="请输入验证码"></el-input>
        </el-form-item>
        <div class='button pull-right' @click='obtainCode' v-if='!codeBtnShow'>获取验证码</div>
        <div class='button1 pull-right' v-if='codeBtnShow'>{{count}}秒后重新发送</div>
        <el-form-item label="新密码" style='margin-top:11px;margin-bottom:30px;' prop="euPassword1">
          <el-input style='width:320px;' type='password' placeholder="请输入新密码" v-model='passwordDto.euPassword1'></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" style='margin-bottom:30px;' prop="euPassword">
          <el-input style='width:320px;' type='password' placeholder="请再次输入密码" v-model='passwordDto.euPassword'></el-input>
        </el-form-item>
        <div class='nextButton pull-left' @click="confirmButton">确定</div>
        <div class='nextButton2 pull-right' @click='cancelButton'>取消</div>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value !== this.passwordDto.euPassword1) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      rules: {
        euMobile: [
          { required: true, message: '请填写手机', trigger: 'blur' },
          { validator: (rule, value, callback) => { if (/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(value) == false) { callback(new Error("请填写正确手机")); } else { callback(); } }, trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请填写验证码', trigger: 'blur' },
          { min: 4, max: 4, message: '长度为4个字符', trigger: 'blur' }
        ],
        euPassword1: [
          { required: true, message: '请填写新密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' },
        ],
        euPassword: [
          { required: true, message: '请再次确认密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' },          
          { validator: validatePass, trigger: 'blur' }
        ]
      },
      passwordDto: {
        code: '',
        euMobile: '',
        euPassword: '',
        euPassword1: ''
      },
      count: 60,
      codeBtnShow: false
    }
  },
  methods: {
    // 更改密码
    confirmButton() {
      var that = this
      this.$refs['passwordDto'].validate((valid) => {
        if (valid) {
          var url = '/api/uaa/eztUsers/code'
          that.$http.post(url, {
            "code": that.passwordDto.code,
            "euMobile": that.passwordDto.euMobile,
            "euPassword": that.passwordDto.euPassword
          }).then(function(res) {
            this.$router.push({ name: '/' })
          },err=>{
            this.$message({
              message:err.body.message,
              type:'warning'
            })
          })
        } else {
          return false
        }
      })
    },
    // 获取验证码
    obtainCode() {
      // this.warning()
      var that = this
      this.$refs['passwordDto'].validateField('euMobile', function(error) {
        if (error) {
        } else {

          var url = 'api/uaa/eztUsers/sendSms';
          that.$http.get(url, {
            params: {
              euMobile: that.passwordDto.euMobile
            }
          }).then(function(res) {
            that.$message({
              message: '验证码发送成功',
              type: 'success'
            });
            that.codeBtnShow = true;
            var timer = setInterval(function() {
              that.count--
              if (that.count <= 0) {
                clearInterval(timer)
                that.codeBtnShow = false
                that.count = 60
              }
            }, 1000)
          }, err => {
            that.$message({
              message: err.body.message,
              type: 'warning'
            });
          })
        }

      })

    },
    // 取消
    cancelButton() {
      this.$router.push({ name: '/' })
    }
  }
}

</script>
<style scoped>
/*html,body,{
  width: 100% !important;
  height:100% !important;
}
.fillCode{
   width: 100%;
  height:100%;
  position: relative;
}*/
/*头部*/

.header {
  height: 60px;
  box-shadow: 3px 3px 10px #ccc;
}

.header .header-left {
  margin-left: 20px;
  float: left;
  font-size: 27px;
  font-weight: 700
}

.header .header-right {
  margin-right: 20px;
  float: right;
}


.header-right-textOne {
  margin-right: 59px;
  color: #333333;
  font-weight: 700;
  font-size: 18px;
  height: 60px;
  line-height: 60px;
}

.header-right-textTwo {
  color: #333333;
  font-size: 18px;
  height: 60px;
  line-height: 60px;
  font-weight: 700;
}








/*表单*/

.fillCodeForm {
  width: 420px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -210px;
  margin-top: -250px;
}

.fillCodeForm h3 {
  margin-left: 150px;
  margin-bottom: 38px;
}

.fillCodeForm .button {
  width: 120px;
  height: 36px;
  background: #5C7BCD;
  line-height: 36px;
  text-align: center;
  color: white;
  margin-top: 11px;
  cursor: pointer
}

.fillCodeForm .button1 {
  width: 120px;
  height: 36px;
  background: #ccc;
  line-height: 36px;
  text-align: center;
  color: white;
  margin-top: 11px;
  cursor: pointer
}

.nextButton {
  width: 150px;
  height: 46px;
  color: white;
  background: #5C7BCD;
  line-height: 46px;
  text-align: center;
  font-size: 17px;
  cursor: pointer;
  margin-left: 100px
}

.nextButton2 {
  width: 150px;
  height: 46px;
  color: white;
  background: #5C7BCD;
  line-height: 46px;
  text-align: center;
  font-size: 17px;
  cursor: pointer
}
</style>





